.unit-list-container {
    display: flex;
    height: 200px;
    margin-bottom: 10px;
  }
  .unit-list-img {
      border: 1px solid #c1c3c3;
      margin:10px 50px;
     height: 200px;
     width: 200px;
     border-radius: 4px;
  }
  .unit-list-img img {
      object-fit: contain;
     width: 100%;
     height: 100%;
     cursor: pointer;
  }
  .unit-list-right-content{
      height: 200px;
      width: 100%;
      margin: 20px 10px;
      display: flex;
      flex-direction: column;
  }
 .unit-list-right-content h2, .unit-list-right-content h4 {
   color: #5e5e5e;
 }
 .unit-list-right-content .price {
     margin: 0;
 }
 .unit-list-discription {
     overflow: hidden;
     height: 100px;
     width: 80%;
 } 
 .unit-list-discription span {
    font-size: 14px;
   
 }
  .unit-list-cart-bar a, .unit-list-cart-bar button {
      color: #212121;
      padding: 10px;
      /* margin: 10px; */
      text-decoration: none;
      font-size: 15px; 
      border-radius: 4px;
      transition: .2s ease-in;
      background: #e2dddd;
      border: 1px solid #F5F5F5;
    }
    .unit-list-cart-bar a:hover, .unit-list-cart-bar button:hover{
      background-color: #f76b6a;
      color: white;
    }
    .unit-list-img {
      position: relative;
    }
    .unit-list-img .cart-status {
      display: flex;
      justify-content: space-between;
      position: absolute;
      width: 30px;
      top:10px;
      right:10px;
      left: 10px;
    }
    .unit-list-cart-bar {
        display: none;
    }
    .unit-list-img:hover .unit-list-cart-bar {
        display: flex;
          justify-content: center;
          position: absolute;
          top:120px;
          right:0px;
          left: 87px;
      }